import React, {useState, useEffect, useReducer} from 'react';
import userReducer from './store/user/reducer';
import * as userAction from './store/user/action';

import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';

// const fetchedAccesToken = async (setAccessToken) => {
// 	const response = await connect.send("VKWebAppGetAuthToken", {"app_id": 6940370, "scope": "friends,status"});
// 	const access_token = await response.data.access_token;
// 	setAccessToken(access_token);
// };

const App = (props) => {
	const userInitialState = {
		photo_200: '',
		first_name: 'Вася',
		last_name: 'Васечкин',
		city: { title: 'New York' }
	}

	const [activePanel, setActivePanel] = useState('home');
	// const [accessToken, setAccessToken] = useState('');

	const [userState, dispatch] = useReducer(userReducer, userInitialState);

	const go = (e) => setActivePanel(e.currentTarget.dataset.to);

	useEffect(() => {
		userAction.fetchedUser(dispatch);
		// fetchedAccesToken(setAccessToken);
	}, []);

	return (
		<View activePanel={activePanel}>
			<Home id="home" fetchedUser={userState} go={go} />
			<Persik id="persik" go={go} />
		</View>
	);
};

export default App;